
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter&family=Noto+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter&family=Noto+Sans&family=Roboto&display=swap');

/* font-family: 'Noto Sans', sans-serif; */
body{
  width: auto;
  margin: 0px;
  padding: 0px;
}
.headertop{
  height: 34px;
  width: auto;
  max-width: 1440px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin : 0 auto;
  background: #66C6A3;
}
.header{
  height: 78px;
  width: auto;
  max-width: 1440px;
  margin : 0 auto;
  background: #282828;
  display: flex;
  align-items: center;
}
.Logo{
  width: auto;
  padding-top: 4px;
  margin-left: 34px;
}
footer{
  height: 153px;
  max-width: 1440px;
  margin : 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: #282828;
}
.footerbanner{
  height: 15px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  margin-bottom: 37px;
  gap: 24px;
  list-style: none;
}
.footerbanner li{
height: 15px;
font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 15px;
color: #EDEDED;
flex: none;
order: 4;
flex-grow: 0;
}
.footerdetail{
height: 45px;
font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 15px;
text-align: center;
color: #EDEDED;
}
ul{
  height: 19px;
  display: flex;
  flex-direction: row;
  padding: 0px;
  gap: 24px;
  list-style:none;
  margin: 0px;
  margin-left: auto;
  margin-right: 37px;
}
ul li{
font-family:'Inter';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 19px;
color: #FFFFFF;
}
a{
  text-decoration: none;
}
li, ol{
  list-style: none;
}
.LoginLayout{
height: 165px;
background: #F5F5F5;
max-width: 1440px;
margin : 0 auto;
}
.Logintitle{
  display: inline-flex;
  flex-direction: column;
  margin-left: 120px;
  margin-top: 77px;
}



.p_insert_body{
  margin: 20px;
  margin-left: 5%;
}
.add:hover{
  color: white;
  background-color: #131523;
}
.add{
  font-weight: bold;  
}
img{
  vertical-align: middle;
}
.strategycircle{
  display: inline-block;
}
a li:hover{
  color: #66C6A3;
}
.active li{
  color: #66C6A3;;
}



.btn {
  background-color: white;
  color: black;
  width: 95%;
  height: 10%;
  display: flex;
  align-items: center;
  padding-left: 5%;
}
.btn.portfoliosubmit{
  background-color:rgb(94,94,94);
  color: black;
  width: 100%;
  height: 25%;
  display: flex;
  padding: 0px;
  align-items: center;
}
.btn.portfoliosubmit div{
  background-color:rgb(94,94,94);
  color: white;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 0px 1px white inset;
}
.btn.portfoliosubmit input{
  background-color:rgb(94,94,94);
  color: white;
  width: 100%;
  height: 100%;
  /* display: flex; */
  box-shadow: 0px 0px 0px 1.5px white inset;
}
.btn.portfoliosubmit input:focus{
  outline: 0;
  box-shadow: 0px 0px 5px white inset;
}
.btn.portfoliosubmit button{
  background-color:rgb(94,94,94);
  color: white;
  width: 100%;
  height: 100%;
  /* display: flex; */
  box-shadow: 0px 0px 0px 1px white inset;
}
.btn.portfoliosubmit button:active{
  background-color:rgb(94,94,94);
  color: black;
  width: 100%;
  height: 100%;
  /* display: flex; */
  box-shadow: 0px 0px 15px white inset;
}
.btn.portfoliosubmit button:focus{
  outline: 0;
}
.strategyname {
  background-color: rgb(94,94,94);
  color: white;
  width: 30%;
  height: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-left: 1%;
}
.strategynum {
  background-color: rgb(94,94,94);
  color: white;
  width: 30%;
  height: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-left: 1%;
}
.strategyrate {
  background-color: rgb(94,94,94);
  color: white;
  width: 38%;
  height: 100%;
  display: inline-flex;
  align-items: center;
}
.percent{
  width: 85%;
  text-align: right;
  background-color: rgb(94,94,94);
  color: white;
}
.btn.active {
  background-color: rgb(94,94,94);
  color: white;
  width: 95%;
  height: 10%;
  display: flex;
  align-items: center;
  padding-left: 5%;
}
/* 부모아래에 있는 자식들을 수직정렬할수 있음 */
.btn.insert{
  justify-content: center;
  background-color: white;
  color: black;
  letter-spacing: 3px;
}
.btn.insert:active{
  box-shadow: 0px 0px 5px #333 inset;
}
.btn.name{
  align-items: center;
  justify-content: center;
  background-color: rgb(94,94,94);
  color: white;
}
.strategytitle{
  height: 10%;
}
.btn input{
  margin-left: auto;
  color: black;
}
.btn.hidden{
  visibility: hidden;
}


.recommandinput{
  display: inline-block;
  border-radius: 6px;
  text-align: center;
  width: 25%;
}
.recommand_inquire{
  margin-top: 20px;
  text-align: center;
}




table {
  border: 1px solid white;
  width: 100%;
  border-collapse:collapse;
}
th{
  text-align: left;
  padding: 8px;
  font-size: 25px;
}
td{
  text-align: left;
  padding: 8px;
  font-size: 18px;
}
th {
  background-color: rgb(245,246,250);
}
tr:hover{
  box-shadow: 1px 1px 4px black inset;
}
caption{
  text-align: left;
  margin-bottom: 40px;
  font-size: 25px;
  font-weight: bold;
}

